<template>
	<v-app>
		<v-content>
			<div class="login">
				<div class="container-login elevation-6">
					<v-row no-gutters>
						<v-col cols="12" lg="4" md="6" sm="12" class="white">
							<v-card tile flat>
								<v-card-text class="black--text">
									<div class="display-1 text-center font-weight-bold icon-color mb-6">
										<div>
											<span class="judul">Admin</span>
											Now
										</div>
									</div>
									<Form/>
								</v-card-text>
							</v-card>
						</v-col>
						<v-col cols="12" lg="8" md="6" cm="12">
							<v-card flat tile>
								<div 
									class="carousel"
									:style="`background-image: url(${require('@/assets/'+dataCarousel.img)})`">
									<v-card-text class="white--text container">
										<div class="display-1 font-weight-light mb-3">
											{{ dataCarousel.title }}
										</div>
										<p class="mb-12">
											{{ dataCarousel.desc }}
										</p>
										<div class="my-6 d-flex">
											<div 
												class="strip mr-3"
												:class="{'active': active === item?true:false}"
												v-for="item in 3" :key="item"
												@click="carousel(item-1)"
											></div>
										</div>
									</v-card-text>
								</div>
							</v-card>
						</v-col>
					</v-row>
				</div>
			</div>
		</v-content>
	</v-app>
</template>

<script>
	import Form from '@/components/Admin/Login/Form'

	export default{
		data: () => ({
			dataCarousel: {},
			active: 1
		}),

		components: { Form },

		created () {
			this.carousel(0)
		},

		computed: {
			akses () {
				var akses = this.$route.name.split('-')
				return {
					name: akses[1],
					jenis: akses[0]
				}
			}
		},

		methods: {
			carousel (val) {
				var data = [
					{ 
						title: 'Easy to Use1', 
						img: 'sign.svg', 
						desc: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam.' 
					},
					{ 
						title: 'Easy to Use2', 
						img: 'abp.svg', 
						desc: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam.' 
					},
					{ 
						title: 'Easy to Use3', 
						img: 'sign.svg', 
						desc: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam.' 
					}
				]

				var hasil = data.filter((data, index) => index === val)

				this.dataCarousel = {...hasil[0]}
				this.active = val+1
			}
		}
	}
</script>

<style scoped>
	.login{
		height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
		background-image: url('../../assets/abp.svg');
		background-size: cover; 
	}
	.container-login{ 
		width: 90%;
		margin: 0 16px;
		border-radius: 10px;
		overflow: hidden;
	}
	.judul{
		padding-bottom: 4px;
		border-bottom: 3px solid purple;
	}
	.carousel{
		height: 486px;
		display: flex;
		align-items: flex-end;
		transition: background-image 0.3s ease-in;
	}
	.strip{
		height: 4px;
		width: 50px;
		display: inline-block;
		background-color: rgba(255, 255, 255, 0.5);		
		border-radius: 50px;
		cursor: pointer;
	}
	.active{
		background-color: rgba(255, 255, 255, 1);		
	}
</style>
